import {
  ActivatedRoute,
  NavigationEnd,
  NavigationStart,
  Router,
  Scroll,
} from "@angular/router";
import { Component, AfterViewInit } from "@angular/core";
import { Title } from "@angular/platform-browser";

@Component({
  selector: "layout-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"],
})
export class HomeComponent implements AfterViewInit {
  title?: string;
  url?: string;

  list = [
    {
      name: "Trang chủ",
      link: "/home",
      icon: "Home",
    },
    {
      name: "Khám bệnh",
      link: "/khambenh",
      icon: "Health",
    },
    {
      name: "Hệ Thống ",
      link: "/khambenh",
      icon: "Settings",
    },
    {
      name: "Danh Mục",
      link: "/khambenh",
      icon: "ms-Icon ms-Icon--AlignJustify",
    },
    {
      name: "Người Dùng",
      link: "/khambenh",
      icon: "ms-Icon ms-Icon--People",
    },
    {
      name: "Kết thúc",
      link: "/khambenh",
      icon: "ms-Icon ms-Icon--StatusCircleErrorX",
    },
  ];

  constructor(
    private titleService: Title,
    private route: ActivatedRoute,
    private router: Router
  ) {}
  ngAfterViewInit(): void {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
      } else if (
        event instanceof NavigationEnd ||
        (event instanceof Scroll && event.routerEvent instanceof NavigationEnd)
      ) {
        if (event instanceof NavigationEnd) {
          this.url = event.url;
        } else {
          this.url = event.routerEvent.url;
        }
        const data = this.router.getCurrentNavigation()?.extras.state;
        if (data) {
          this.title = data["title"];
          this.titleService.setTitle(
            `${this.title ? this.title + " | " : ""} HIS`
          );
        } else {
          this.getRouterActive(this.route);
        }
      } else {
        this.getRouterActive(this.route);
      }
    });
    window.dispatchEvent(new Event("resize"));
  }
  getRouterActive(active: ActivatedRoute) {
    if (active.firstChild) {
      this.getRouterActive(active.firstChild);
    } else {
      active.data.subscribe((data) => {
        if (data) {
          this.title = data["title"];
          this.titleService.setTitle(
            `${this.title ? this.title + " | " : ""} HIS`
          );
        }
      });
    }
  }
}
